.Form_Form__2fzsQ {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

@media (max-width: 600px) {
    .Form_Form__2fzsQ {
        -webkit-flex-direction: column;
                flex-direction: column;
    }
}
.Result_Result__C2RzB {
    margin: 0 50px;
    padding-top: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.Result_Result__C2RzB h3,
.Result_Result__C2RzB h4 {
    font-weight: normal;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
